import * as React from 'react';
import { Plugin } from '@ali/4ever-cangjie';
import { InlinePlaceholder } from '@ali/4ever-component';
import { PlaceHolderPluginConfig } from '../types';
import { Placeholder } from '@ali/4ever-factory';

export default function createRenderNode(
  config: PlaceHolderPluginConfig,
): Plugin['renderNode'] {
  const { renderText } = config;
  return {
    ph: (props) => {
      return <Placeholder {...props} />;
    },
    iph: (props, controller) => {
      return (
        <InlinePlaceholder
          {...props}
          editor={controller}
          renderText={renderText}
        />
      );
    },
  };
}
